.navbar {
 background-color: #222;
 padding:10px 30px;
 align-items: center;
 justify-content: space-between;
 display: flex;
 height: 80px;
}

.leftSide {
 flex: 50%;
 display: flex;
 align-items: center;
}

.leftSide img {
 width:70px;
 height: 40px;
}

.leftSide::after{
content: Enactus;
color:white;
font-size: 12px;
margin-top: 5px;
}

.rightSide {
 display: flex;
 gap: 20px;
}

.logoText{
    color:white;
    font-size: 14px;
    margin-top:5px;
}

/*had hna mzyn*/
.rightSide{
    color: white;
    text-decoration: none;
    font-weight: 500;
}

.navbar {
    color: white;
    text-decoration: none;
    margin:20p;
}
.navbar.rightSide button{
    background-color: transparent;
    border: none;
    color: white;
    cursor: pointer;
}


.navbar.rightSide svg{
    font-size: 40px;
}

